export default {
	data() {
		return {
			base:{
				CustomBar:this.CustomBar
			},
			title: '加载中',
			details: {
				content: ''
			},
			loadShow: true
		}
	},
	methods: {},
	onLoad(e) {
		if (e.title) {
			this.title = e.title
		}
		if (e.id) {
			this.$api.cmsGetDetails({
				id: e.id,
			}, (res) => {
				if (res.code) {
					this.details.content = res.data.content;
					this.loadShow = false
				}
			})
		}
	}
}
